import React from "react";

const PrivacyPolicy = () => {
  return (
    <>
      <span id="policy" className="title">
        Privacy Policy
      </span>
      <p className="description">
        This Privacy Policy describes how „we“ or „spherium.finance“ will manage
        end user’s personal information which we collect as a result of your use
        of the website including any tablet or mobile phone applications that
        are owned and/or operated by us or whenever we otherwise interact with
        the Users. Spherium is committed to ensuring that the personal
        information we collect about you is protected and is used, stored and
        disclosed in accordance with the Privacy Act and this Privacy Policy.
      </p>
      <p className="subtittle">Website Use</p>
      <p className="description">
        In using the website, you agree to be bound by the terms of this Privacy
        Policy. Spherium may review the Privacy Policy periodically and reserves
        the right to change the Privacy Policy at any time at its discretion by
        posting such changes on the website. Changes will be effective
        immediately when posted on the website. The end users’ continued use of
        the website following posting will constitute your acceptance of those
        changes.
        <br />
        <br />
        This Privacy Policy will not apply to websites which are linked to the
        website and over which we have no control.
      </p>
      <p className="subtittle">Personal Information</p>
      <p className="description">
        The kinds of Personal Information that we may collect from you are the
        following:
        <br />
        <br />
        ● Name;
        <br />
        ● Email address;
        <br />
        ● Personally submitted preferences;
        <br />
        ● Location data;
        <br />
        ● IP address.
        <br />
        <br />
        We will collect your Personal Information only by lawful and fair means
        and not in an intrusive way to operate its Service, and to provide you
        with the following information:
        <br />
        <br />
        ● If you wish to subscribe to the newsletter or other marketing
        communications;
        <br />
        ● To update you on technical progress of Spherium;
        <br />
        ● To provide services to you as a User;
        <br />
        ● To identify you as a User for security purposes and to comply with our
        legal obligations;
        <br />
        ● To maintain your User account;
        <br />
        <br />
        ● To upgrade and enhance your experience within the website or over the
        telephone, or to tailor or develop information, services or products to
        suit your needs which may include market research and conducting
        promotions;
        <br />
        <br />
        ● To tell you about our products or services that we think may be of
        interest to you by communicating with you via email, SMS or telephone;
        <br />
        <br />
        ● To create aggregate data about Users through demographic profiling,
        statistical analysis of the database to provide to potential and
        existing client, and to allow for more efficient operation of Spherium’s
        business;
        <br />
        <br />
        ● To comply with our legal and statutory obligations incl. for taking
        appropriate action if we have reason to suspect that unlawful activity
        or misconduct of a serious nature has been, is being or may be engaged
        in that relates to our functions and activities;
        <br />
        <br />
        ● To establish, exercise or defend any legal claims.
        <br />
        <br />
        By becoming a User or otherwise choosing to provide us with Personal
        Information you consent to the collecting, using and disclosing your
        Personal Information for the above purposes.
        <br />
        <br />
        You may choose to deal with us on an anonymous basis or to use a
        pseudonym unless it is not practicable for us to deal with individuals
        who we cannot identify or we are required by law to only deal with
        identified individuals. Also, if you do not provide us with the Personal
        Information we request, we may be unable to process your request to
        become a User, provide you with our services or respond to your enquiry
        or complaint.
      </p>
      <p className="subtittle">Direct Marketing and Opting Out</p>
      <p className="description">
        Occasionally we may use your Personal Information to inform you about
        our new products or services or about promotional activities which we
        believe may be of interest or of benefit to you. We may do this via
        email, SMS, telephone or mail. If you no longer wish to receive
        marketing or promotional material at all or in any particular form, you
        may contact us at any time by email.
        <br />
        <br />
        From time to time we may contact you in relation to the management and
        administration of your account. These communications can be via any of
        the modes of contact recorded when registering as a User. Such
        communication does not affect your opt-in or opt-out status for direct
        marketing communications.
      </p>
      <p className="subtittle">
        Protection and Management of your Personal Information
      </p>
      <p className="description">
        We take all reasonable steps to ensure that the Personal Information
        which we collect, use or disclose is correct and is stored in a secure
        environment which is accessed only by authorized persons.
        <br />
        <br />
        We will further destroy or permanently anonymize the Personal
        Information we hold when it is no longer required for any purpose
        permitted under the APPs including our legal or operational obligations.
        <br />
        <br />
        You and we acknowledge that no data transmission over the Internet is
        totally secure. Accordingly, we do not warrant the security of any
        information which you transmit to it. Any information which you transmit
        to us is transmitted at your own risk. However, once we receive your
        transmission, we will take reasonable steps to protect your Personal
        Information from misuse, loss and unauthorized access, modification and
        disclosure including by using password protected systems and databases
        and Secure Socket Layer (SSL) technology.
        <br />
        <br />
        Our employees, agents and contractors are required to maintain the
        confidentiality of Users’ Personal Information and trading behavior.
        <br />
        <br />
        Information posted on bulletin boards or communicated within a social
        media environment (for example, LinkedIn, Facebook, Twitter, Medium)
        becomes public information; subsequently, we cannot guarantee the
        security of this type of disclosed information.
        <br />
        <br />
        We take seriously the responsibility to exclude children from access to
        our services. We will not accept their information for the purposes of
        accessing or using the Service. It is however, ultimately the
        responsibility of parents or guardians to monitor their children’s
        Internet activities including where appropriate by using Internet
        screening software.
        <br />
        <br />
        Remember to always log out of your account when you have completed your
        time on the website. This is particularly important if you share a
        computer with another person. You are responsible for the security of
        and access to your own computer, mobile device or any other handset used
        to access the website.
        <br />
        <br />
        Ultimately, you are solely responsible for maintaining the secrecy of
        your username, password and any account information. Please be careful
        whenever using the Internet and our website.
      </p>
      <p className="subtittle">Access to Personal Information</p>
      <p className="description">
        You may access the Personal Information collected by Spherium by
        contacting us; we will give you access to your Personal Information in
        the manner requested if that is possible and within a reasonable period.
        If we refuse your request or cannot give you access in the manner you
        have requested, we will do what we can to meet your requirements by
        other means. We may not be required to give you access to your Personal
        Information in certain circumstances which are set out in the Terms of
        Service/Use including where it may have an unreasonable impact on other
        individual's privacy. If we refuse access for such reasons, we will
        advise you in writing of the refusal and our reasons and the complaint
        mechanisms available to you.
        <br />
        <br />
        If you have any queries, requests for access or correction or complaints
        relating to the handling of your personal information, please contact us
        by email.
      </p>
      <p className="subtittle">Use of Website</p>
      <p className="description">
        By using the www.spherium.finance website, you agree to the collecting
        and processing your Personal Information on the basis explained in this
        Privacy Policy.
      </p>
      <p className="subtittle">Updates to this Privacy Policy</p>
      <p className="description">
        We may review, change and update this Privacy Policy to reflect our
        current practices and obligations. We will publish our current Privacy
        Policy on our website and the changes will take effect at the time of
        publishing. You should review this privacy policy regularly and remain
        familiar with its terms.
      </p>
    </>
  );
};

export default PrivacyPolicy;
